
.home-parallax {
  height: 360px;
  width: 100%;
  overflow: hidden;
  position: relative;
  text-align: center;
}
.home-parallax .home-parallax-content {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: #fff;
}
.home-parallax .home-parallax-content h1 {
  margin: 0;
}
.introduction {
  color: #fff;
  font-size: 24px;
}
.water-fall {
  margin-top: 40px;
}
.water-fall:after {
  content: '';
  clear: both;
}
.water-fall-list {
  float: left;
}
.member {
  width: 100%;
  position: relative;
  overflow: hidden;
}
.member:hover .member-hover-layer {
  opacity: 1;
}
.member-hover-layer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  background-color: rgba(0,0,0,0.6);
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.member-photo {
  width: 100%;
  display: block;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.member-hover-photo {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
}
.member-footer {
  -webkit-transition: height 0.4s ease;
  transition: height 0.4s ease;
  height: 0;
  overflow: hidden;
}
.member-love {
  position: absolute;
  right: 10px;
  bottom: 10px;
}
.heart-c {
  width: 40px;
  height: 40px;
  cursor: pointer;
}
#app {
  width: 100%;
}
::-moz-selection {
  background: none;
}
::selection {
  background: none;
}